	--birds-bright: #FF5629;
	--birds-shadow: #DDDDDD;
}

.birds {
	position: relative;
	overflow: hidden;
  width: 800px;
  height: 500px;
  margin: auto;
}

.birds__hatdove {
		position: absolute;
		top: 70px;
